@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  background-color: blue;
  font-family: "Roboto", sans-serif;
}

nav {
  padding: 0.25rem;
  background-color: blue;
  width: 60%;
  margin: 0 auto;
}

nav ul {
  display: -webkit-flex;
  display: flex;
  list-style: none;
  -webkit-justify-content: center;
          justify-content: center;
  padding-right: 50px;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
  font-size: 1.25rem;
  font-weight: 300;
  padding: 2em;
  text-transform: lowercase;
}

nav ul li a:hover {
  text-transform: uppercase;
}

.slide {
  background-color: gray;
  width: 60%;
  margin: 0 auto;
  text-align: center;
  font-weight: 300;
  height: 400px;
}

.fans-box {
  width: 60%;
  margin: 0 auto;
  text-align: center;
  background-color: #fff;
}

.fans-box p {
  font-size: 36px;
  padding: 2rem;
  background: red; /* For browsers that do not support gradients */ /* For Safari 5.1 to 6.0 */ /* For Opera 11.1 to 12.0 */ /* For Firefox 3.6 to 15 */
  background: linear-gradient(to right, orange, yellow, green, cyan, blue, violet); /* Standard syntax (must be last) */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.img-responsive {
  width: 100%;
  height: 100%;
  background: url(/static/media/header.7e815672.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.contact-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.profile {
  text-align: center;
  padding: 3rem;
}

.img-profile-themuh {
  background: url(/static/media/themuh.3b80e2b1.jpg);
  border-radius: 50%;
  width: 200px;
  height: 200px;
}

.img-profile-macrowave {
  background: url(data:image/jpeg;base64,);
  border-radius: 50%;
  width: 200px;
  height: 200px;
}

section {
  background-color: #fff;
  width: 60%;
  margin: 0 auto;
  height: 100%;
}

.container {
  padding: 5rem;
}

.greeting-box {
  margin-top: 3rem;
  border: 1px solid blue;
  border-radius: 3px;
  height: 200px;
  text-align: center;
  padding-top: 7rem;
}

hr {
  border: 1px solid blue;
}

.schedule-list {
  text-align: center;
  margin-top: 3rem;
}

footer {
  padding: 0.25rem;
  background-color: blue;
  width: 60%;
  margin: 0 auto;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
}

.not-found {
  font-size: 50px;
  font-weight: 300;
}

iframe {
  margin-top: 3rem;
}

@media (max-width: 600px) {
  nav {
    margin: 0 auto;
    width: 90%;
    
  }
  
  nav ul {
    display: -webkit-flex;
    display: flex;
    list-style: none;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  nav ul li {
    width: 100%;
      text-align: center;
    border: 1px #fff solid;
    padding: .3rem;
    border-radius: 5px;
    margin-top: .25rem;
  }

  nav ul li a {
    padding: .25em;
    width: 100%;
  }

  .slide {
    background-color: gray;
    width: 100%;
    margin: 0 auto;
    text-align: center;
    font-weight: 300;
    height: 200px;
  }
  
  .img-responsive {
    width: 100%;
    height: 100%;
    background: url(/static/media/header.7e815672.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  
  .contact-list {
    margin-top: 3rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  .profile {
    text-align: center;
    padding: .5rem;
  }
  
  .img-profile-themuh {
    background: url(/static/media/themuh.3b80e2b1.jpg);
    border-radius: 50%;
    width: 200px;
    height: 200px;
  }
  
  .img-profile-macrowave {
    background: url(data:image/jpeg;base64,);
    border-radius: 50%;
    width: 200px;
    height: 200px;
  }
  
  section {
    background-color: #fff;
    width: 100%;
    margin: 0 auto;
    height: 100%;
  }
  
  .container {
    padding: 2rem;
  }
  
  .greeting-box {
    margin-top: 3rem;
    border: 1px solid blue;
    border-radius: 3px;
    height: 200px;
  }
  
  hr {
    border: 1px solid blue;
  }
  
  .schedule-list {
    text-align: center;
    margin-top: 3rem;
  }
  
  footer {
    padding: 0.25rem;
    background-color: blue;
    width: 60%;
    margin: 0 auto;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    font-weight: 700;
  }
  
  .not-found {
    font-size: 50px;
    font-weight: 300;
  }
  
  iframe {
    margin-top: 3rem;
  }
}
